import * as React from "react"
import { graphql, PageProps } from "gatsby"
import ReactMarkdown from "react-markdown"
import remarkGfm from 'remark-gfm'
import { convertImageUrl } from '../common/image'
import Layout from "../components/layout"
import icon from "../images/icon.png"

type DataProps = {
    slug: string,
    title: string,
    author?: { name: string },
    category?: { name: string },
    body: string
}

const Post = (page: PageProps) => {
    return (
        <Layout>
            <div>   
                <img width={52} height={52} src={icon} />
            </div>
            <h1>{(page.pageContext as DataProps).title}</h1>
            {(page.pageContext as DataProps).author?.name}
            <div>
                <ReactMarkdown
                    children={convertImageUrl((page.pageContext as DataProps).body)}
                    remarkPlugins={[remarkGfm]}
                />
            </div>
        </Layout>
    )
}

export default Post
